<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择公司"
              clearable
              @change="onChangeSearchCompany"
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              clearable
              @change="onChangeSearchSpot"
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="">
            <el-select v-model="selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker
              v-model="searchValidateForm.startEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :value-format="valueFormat"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

          <!-- 请选择状态 start -->
          <el-form-item label="" prop="payState">
            <el-select
              v-model="searchValidateForm.payState"
              filterable
              placeholder="请选择状态"
              clearable
            >
              <el-option
                v-for="(item, index) in payStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA" class="exportA" :href="downloadUrl" download></a>
        <!-- 导出 Excel end -->

        <!-- 新增 start -->
        <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
        <!-- 新增 end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        title="编辑结算流水信息"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="130px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 财务付分润金额/元 start -->
          <el-form-item
            label="财务付分润金额"
            prop="moneyOut"
            v-if="validateForm.type == '2'"
          >
            <el-input
              type="number"
              v-model="validateForm.moneyOut"
              autocomplete="off"
              placeholder="请输入财务付分润金额"
            ></el-input>
          </el-form-item>
          <!-- 财务付分润金额/元 end -->

          <!-- 财务收分润金额/元 start -->
          <el-form-item
            label="财务收分润金额"
            prop="moneyIn"
            v-if="validateForm.type == '1'"
          >
            <el-input
              type="number"
              v-model="validateForm.moneyIn"
              autocomplete="off"
              placeholder="请输入财务收分润金额"
            ></el-input>
          </el-form-item>
          <!-- 财务收分润金额/元 end -->

          <!-- 财务收分润金额/元 start -->
          <el-form-item label="财务收配件维修费" prop="spotRepairIn">
            <el-input
              type="number"
              v-model="validateForm.spotRepairIn"
              autocomplete="off"
              placeholder="请输入财务收配件维修费"
            ></el-input>
          </el-form-item>
          <!-- 财务收配件维修费/元 end -->

          <!-- 分润结算状态 start -->
          <el-form-item label="分润结算状态" prop="prStatus">
            <el-select
              style="width: 100%"
              v-model="validateForm.psStatus"
              filterable
              placeholder="请选择分润结算状态"
              clearable
            >
              <el-option
                v-for="(item, index) in psStatusOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 分润结算状态 end -->

          <!-- 配件维修结算状态 start -->
          <el-form-item label="配件维修结算状态" prop="rpStatus">
            <el-select
              style="width: 100%"
              v-model="validateForm.rpStatus"
              filterable
              placeholder="请选择配件维修结算状态"
              clearable
            >
              <el-option
                v-for="(item, index) in rpStatusOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 配件维修结算状态 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->

          <!-- 付款人 start -->
          <!-- <el-form-item label="付款人" prop="paymentName">
            <el-input type="text" v-model="validateForm.paymentName" autocomplete="off" placeholder="请输入付款人" :disabled="true"></el-input>
          </el-form-item> -->
          <!-- 付款人 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        收款单位: 'companyName',
        景区名称: 'spotName',
        月份: 'month',
        '微信支付金额/元': 'wechat',
        '储值支付金额/元': 'saving',
        '储值抵扣+微信支付金额/元': 'savingWechat',
        '押金抵扣金额/元': 'deposit',
        '总分润基数金额/元': 'total',
        手续费费率: 'charge',
        '可分润金额(手续费已扣)': 'totalCharge',
        分润比例: 'proportion',
        '分润金额(手续费已扣、未扣税点)': 'totalProportion',
        税点: 'tax',

        '扣税后分润金额(支出)/元': 'totalTaxEx',
        '扣税后分润金额(收入)/元': 'totalTax',

        机器数量: 'robotCount',
        流水金额核实人: 'accountCheckName',

        '财务付分润金额/元': 'moneyOut',
        '财务收分润金额/元': 'moneyIn',

        '配件申请费用/元': 'parts',
        '上报故障新发配件费用/元': 'partsNew',
        '上报故障维修费用/元': 'fault',
        '损坏赔偿配件费用/元(配件价格-游客已支付费用)': 'compensate',
        '总配件维修金额/元': 'repair',
        配件维修金额核实人: 'repairCheckName',
        '景区承担配件维修费/元': 'spotRepair',
        '财务收配件维修费/元': 'spotRepairIn',
        流水支出类型: 'type',
        备注: 'notes',
        分润付款人: 'paymentName',
        // 核实状态: 'status',
        分润结算状态: 'psStatus',
        配件维修结算状态: 'rpStatus',
        运营对账时间: 'reconciliationTime',
        财务付款时间: 'payTime'
      },
      total: 0,
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      payStateOptions: [
        {
          value: '0',
          label: '未核实'
        },
        {
          value: '1',
          label: '已核实'
        },
        {
          value: '2',
          label: '已付款/已收款'
        }
      ], // 搜索的核实状态选项

      psStatusOptions: [
        {
          value: 0,
          label: '待核实'
        },
        {
          value: 1,
          label: '已核实'
        },
        {
          value: 2,
          label: '已付款/已收款'
        }
      ], // 分润结算状态选项

      rpStatusOptions: [
        {
          value: 0,
          label: '待核实'
        },
        {
          value: 1,
          label: '已核实'
        },
        {
          value: 2,
          label: '已收款'
        }
      ], // 配件维修结算状态选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'monthrange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM', // 日期格式

      searchValidateForm: {
        companyId: '', // 公司id
        spotId: '', // 景区id
        startEndDate: '', // 起止日期
        payState: '' // 支付状态
      }, // 搜索表单验证对象
      searchVF: {
        companyId: '', // 公司id
        spotId: '', // 景区id
        startEndDate: '', // 起止日期
        payState: '' // 支付状态
      }, // 搜索需要提交的表单
      validateForm: {}, // 表单验证对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: '', // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: '',

      // 搜索表单规则
      searchValidateFormRules: {},

      // 提交表单规则
      validateFormRules: {
        // 付款金额
        moneyOut: [
          {
            validator: checkNumberMethod('付款金额'),
            trigger: ['blur', 'change']
          }
        ],

        // 收款金额
        moneyIn: [
          {
            validator: checkNumberMethod('收款金额'),
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
    this.getValidateForm()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
  },
  mounted() {},
  methods: {
    // 获取查询表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        companyId: null,
        spotId: null
      }
      // console.log(this.validateForm);
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var companyId = this.searchVF.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginDate = null
        endDate = null
      } else {
        beginDate = this.searchVF.startEndDate[0]
        endDate = this.searchVF.startEndDate[1]
      }

      var payState = this.searchVF.payState
      if (payState == '' || !payState) {
        payState = null
      }

      this.tableLoading = true
      const { data: res } = await this.$http.get('/system/account_close/list', {
        params: {
          company: companyId,
          spot: spotId,
          beginDate: beginDate,
          endDate: endDate,
          type: payState,
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      this.tableData = res.data
      this.total = res.total
      this.realIncome = res.type

      this.tableLoading = false
    },

    /**
     * 导出Excel表格的全部数据
     */
    getExportTableData() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }
      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = null
        endDate = null
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }
      var payState = this.searchValidateForm.payState
      if (payState == '' || !payState) {
        payState = null
      }

      var data = {
        url: '/system/account_close/download',
        companyId: companyId,
        spotId: spotId,
        beginDate: beginDate,
        endDate: endDate,
        type: payState
      }
      this.downloadUrl = createUrl(data)
      // console.log(this.downloadUrl);
      this.downloadLoading = true

      this.$nextTick(() => {
        document.getElementById('exportA').click()
        this.downloadLoading = false

        this.$message({
          type: 'success',
          message: '已成功导出Excel表格！',
          offset: 100,
          center: true,
          duration: 1000
        })
      })

      // const { data: res } = await this.$http.get("/system/account_close/download", {
      //   params: {
      //     companyId: companyId,
      //     spotId: spotId,
      //     beginDate: beginDate,
      //     endDate: endDate,
      //     type: payState
      //   }
      // })

      // this.exportTableData = res.data
    },

    /**
     * 编辑签约信息
     */
    async editTableData(data) {
      const { data: res } = await this.$http.post(
        '/system/account_close/update',
        {
          ...data,
          status: 2
        }
      )
      // console.log(res);
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.companyId == this.searchVF.companyId &&
        this.searchValidateForm.spotId == this.searchVF.spotId &&
        this.selectDateState == this.searchSelectDateState &&
        this.searchValidateForm.startEndDate == this.searchVF.startEndDate &&
        this.searchValidateForm.payState == this.searchVF.payState
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.companyId = this.searchValidateForm.companyId
      this.searchVF.spotId = this.searchValidateForm.spotId
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate
      this.searchVF.payState = this.searchValidateForm.payState

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/spotList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SPOT_ID
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      // 如果spotId为空，则将支付方式置为空
      if (!spotId) {
        this.searchValidateForm.paymentType = []
      }
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.companyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.companyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.companyId = searchCompanyOptions[0].COMPANY_ID
      }
    },
    /** 搜索表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
        this.valueFormat = 'yyyy-MM'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'daterange'
        this.valueFormat = 'yyyy-MM-dd'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
        this.valueFormat = 'yyyy'
      }
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()

      this.selectDateState = '1'
      this.dateType = 'monthrange'
      this.currentPage = 1

      // 重新获取表格数据
      this.getTableData()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.getExportTableData()
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          // this.$nextTick(() => {
          //   var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
          //   /* 从表生成工作簿对象 */
          //   var wb = XLSX.utils.table_to_book(
          //     document.querySelector('#outTable'),
          //     xlsxParam
          //   )
          //   /* 获取二进制字符串作为输出 */
          //   var wbout = XLSX.write(wb, {
          //     bookType: 'xlsx',
          //     bookSST: true,
          //     type: 'array'
          //   })
          //   try {
          //     FileSaver.saveAs(
          //       //Blob 对象表示一个不可变、原始数据的类文件对象。
          //       //Blob 表示的不一定是JavaScript原生格式的数据。
          //       //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
          //       //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
          //       new Blob([wbout], {
          //         type: 'application/octet-stream'
          //       }),
          //       //设置导出文件名称
          //       "结算流水" + '.xlsx'
          //     )
          //   } catch (e) {
          //     if (typeof console !== 'undefined') console.log(e, wbout)
          //   }
          //   this.downloadLoading = false
          //   return wbout
          // })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象

      // rowObj.paymentName = "张三" // 当前登录用户
      this.validateForm = rowObj

      if (rowObj.type == 1) {
        // 收入
        this.psStatusOptions = [
          {
            value: 0,
            label: '待核实'
          },
          {
            value: 1,
            label: '已核实'
          },
          {
            value: 2,
            label: '已收款'
          }
        ]
      } else if (rowObj.type == 2) {
        // 支出
        this.psStatusOptions = [
          {
            value: 0,
            label: '待核实'
          },
          {
            value: 1,
            label: '已核实'
          },
          {
            value: 2,
            label: '已付款'
          }
        ]
      }
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var data = Object.assign({}, this.validateForm) // 防止数据错乱先深拷贝一波

          console.log('编辑公司信息')
          this.editTableData(data)

          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '收入'

            // 如果分润结算状态是2
            if(row['psStatus'] == 2) {
              // 根据当前 type==2 连设置为 3
              row['psStatus'] = 3
            }

            // 如果分润结算状态是2
            if(row['psStatus'] == 2) {
              // 根据当前 type==2 连设置为 3
              row['psStatus'] = 3
            }
          } else if (row[value] == 2) {
            result = '支出'

            // 如果分润结算状态是2
            if(row['psStatus'] == 2) {
              // 根据当前 type==2 连设置为 2
              row['psStatus'] = 2
            }
          } else {
            result = '无'
          }
        } else if (value == 'status') {
          // 核实状态
          if (row[value] == 0) {
            result = '未核实'
          } else if (row[value] == 1) {
            result = '已核实'
          } else if (row[value] == 2) {
            result = '已付款'
          } else if (row[value] == 3) {
            result = '已收款'
          } else {
            result = '无'
          }
        } else if (value == 'psStatus') {
          // 分润结算状态
          if (row[value] == 0) {
            result = '待核实'
          } else if (row[value] == 1) {
            result = '已核实'
          } else if (row[value] == 2) {
            result = '已付款'
          } else if (row[value] == 3) {
            result = '已收款'
          } else {
            result = '无'
          }
        } else if (value == 'rpStatus') {
          // 配件维修结算状态
          if (row[value] == 0) {
            result = '待核实'
          } else if (row[value] == 1) {
            result = '已核实'
          } else if (row[value] == 2) {
            result = '已收款'
          } else {
            result = '无'
          }
        } else if (
          value == 'wechat' ||
          value == 'saving' ||
          value == 'savingWechat' ||
          value == 'deposit' ||
          value == 'total' ||
          value == 'totalCharge' ||
          value == 'proportion' ||
          value == 'totalProportion' ||
          value == 'tax' ||
          value == 'totalTaxEx' ||
          value == 'totalTax' ||
          value == 'moneyOut' ||
          value == 'moneyIn' ||
          value == 'parts' ||
          value == 'partsNew' ||
          value == 'fault' ||
          value == 'compensate' ||
          value == 'repair' ||
          value == 'spotRepair' ||
          value == 'spotRepairIn'
        ) {
          result = (row[value] && Number(row[value]).toFixed(2)) || '0.00'
        } else if (value == 'charge') {
          result = row[value] && (row[value] * 100).toFixed(2) + '%'
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

